.skills-section {
  background: linear-gradient(135deg, #1a1a1a 0%, #2d2d2d 100%);
  padding: 6rem 0;
  position: relative;
  overflow: hidden;
}

.skills-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url("data:image/svg+xml,%3Csvg width='60' height='60' viewBox='0 0 60 60' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%234facfe' fill-opacity='0.05'%3E%3Cpath d='M36 34v-4h-2v4h-4v2h4v4h2v-4h4v-2h-4zm0-30V0h-2v4h-4v2h4v4h2V6h4V4h-4zM6 34v-4H4v4H0v2h4v4h2v-4h4v-2H6zM6 4V0H4v4H0v2h4v4h2V6h4V4H6z'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
  opacity: 0.1;
}

.skills-header {
  text-align: center;
  margin-bottom: 3rem;
}

.section-title {
  font-size: 2.5rem;
  color: white;
  margin-bottom: 1rem;
  background: linear-gradient(135deg, #00f2fe 0%, #4facfe 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  display: inline-block;
}

.section-subtitle {
  color: #a0aec0;
  font-size: 1.1rem;
  max-width: 600px;
  margin: 0 auto;
}

.skills-categories {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 3rem;
  flex-wrap: wrap;
  padding: 0 1rem;
}

.category-btn {
  background: transparent;
  border: 2px solid #4facfe;
  color: #4facfe;
  padding: 0.5rem 1.5rem;
  border-radius: 25px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  outline: none;
  min-width: 120px;
  text-align: center;
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.category-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background: linear-gradient(135deg, #00f2fe 0%, #4facfe 100%);
  transition: width 0.3s ease;
  z-index: -1;
}

.category-btn:hover::before {
  width: 100%;
}

.category-btn:hover {
  color: white;
  transform: translateY(-2px);
}

.category-btn.active {
  background: linear-gradient(135deg, #00f2fe 0%, #4facfe 100%);
  color: white;
  border: none;
  box-shadow: 0 5px 15px rgba(79, 172, 254, 0.4);
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 2rem;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}

.skill-card {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  padding: 1.5rem;
  transition: all 0.3s ease;
}

.skill-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  border-color: rgba(79, 172, 254, 0.3);
}

.skill-header {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1rem;
}

.skill-header i {
  font-size: 2rem;
  background: linear-gradient(135deg, #00f2fe 0%, #4facfe 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.skill-name {
  color: white;
  font-size: 1.25rem;
  margin: 0;
}

.skill-progress {
  background: rgba(255, 255, 255, 0.1);
  height: 8px;
  border-radius: 4px;
  overflow: hidden;
  position: relative;
}

.progress-bar {
  height: 100%;
  background: linear-gradient(135deg, #00f2fe 0%, #4facfe 100%);
  border-radius: 4px;
  position: relative;
  transition: width 1s ease-in-out;
}

.progress-bar::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(
    90deg,
    rgba(255, 255, 255, 0) 0%,
    rgba(255, 255, 255, 0.1) 50%,
    rgba(255, 255, 255, 0) 100%
  );
  animation: shimmer 3s linear infinite;
  transform: translateX(-100%);
}

.progress-text {
  position: absolute;
  right: 0;
  top: -20px;
  color: #a0aec0;
  font-size: 0.9rem;
  font-weight: 600;
}

@keyframes shimmer {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}

@media (max-width: 1024px) {
  .skills-section {
    padding: 5rem 0;
  }

  .skills-header {
    margin-bottom: 2.5rem;
  }

  .section-title {
    font-size: 2.2rem;
  }

  .section-subtitle {
    font-size: 1rem;
    max-width: 500px;
  }

  .skills-categories {
    gap: 0.8rem;
    margin-bottom: 2.5rem;
  }

  .category-btn {
    padding: 0.4rem 1.2rem;
    font-size: 0.9rem;
    min-width: 110px;
  }

  .skills-grid {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 1.5rem;
  }

  .skill-card {
    padding: 1.2rem;
  }

  .skill-header i {
    font-size: 1.8rem;
  }

  .skill-name {
    font-size: 1.1rem;
  }
}

@media (max-width: 768px) {
  .skills-section {
    padding: 4rem 0;
  }

  .skills-header {
    margin-bottom: 2rem;
  }

  .section-title {
    font-size: 2rem;
  }

  .section-subtitle {
    font-size: 0.9rem;
    max-width: 400px;
  }

  .skills-categories {
    gap: 0.5rem;
    margin-bottom: 2rem;
    padding: 0 0.5rem;
  }

  .category-btn {
    padding: 0.4rem 1rem;
    font-size: 0.85rem;
    min-width: 90px;
  }

  .skills-grid {
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 1.2rem;
    padding: 0 0.5rem;
  }

  .skill-card {
    padding: 1rem;
  }

  .skill-header {
    gap: 0.8rem;
    margin-bottom: 0.8rem;
  }

  .skill-header i {
    font-size: 1.6rem;
  }

  .skill-name {
    font-size: 1rem;
  }

  .skill-progress {
    height: 6px;
  }

  .progress-text {
    font-size: 0.8rem;
    top: -18px;
  }
}

@media (max-width: 480px) {
  .skills-section {
    padding: 3rem 0;
  }

  .skills-header {
    margin-bottom: 1.5rem;
  }

  .section-title {
    font-size: 1.8rem;
  }

  .section-subtitle {
    font-size: 0.85rem;
    max-width: 300px;
  }

  .skills-categories {
    gap: 0.4rem;
    margin-bottom: 1.5rem;
  }

  .category-btn {
    padding: 0.3rem 0.8rem;
    font-size: 0.8rem;
    min-width: 80px;
  }

  .skills-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }

  .skill-card {
    padding: 0.8rem;
  }

  .skill-header {
    gap: 0.6rem;
    margin-bottom: 0.6rem;
  }

  .skill-header i {
    font-size: 1.4rem;
  }

  .skill-name {
    font-size: 0.9rem;
  }

  .skill-progress {
    height: 5px;
  }

  .progress-text {
    font-size: 0.75rem;
    top: -16px;
  }
} 