.footer {
  background: linear-gradient(135deg, #1a1a1a 0%, #2d2d2d 100%);
  color: #fff;
  padding: 4rem 0 2rem;
  position: relative;
  overflow: hidden;
}

.footer::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url("data:image/svg+xml,%3Csvg width='60' height='60' viewBox='0 0 60 60' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%234facfe' fill-opacity='0.05'%3E%3Cpath d='M36 34v-4h-2v4h-4v2h4v4h2v-4h4v-2h-4zm0-30V0h-2v4h-4v2h4v4h2V6h4V4h-4zM6 34v-4H4v4H0v2h4v4h2v-4h4v-2H6zM6 4V0H4v4H0v2h4v4h2V6h4V4H6z'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
  opacity: 0.1;
}

.footer-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  position: relative;
  z-index: 1;
}

.footer-top {
  display: grid;
  grid-template-columns: 1fr 2fr;
  gap: 3rem;
  margin-bottom: 3rem;
}

.footer-info {
  max-width: 400px;
}

.footer-title {
  font-size: 1.8rem;
  margin-bottom: 1rem;
  background: linear-gradient(135deg, #00f2fe 0%, #4facfe 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  display: inline-block;
}

.footer-subtitle {
  color: #4facfe;
  font-size: 1.1rem;
  margin-bottom: 1rem;
}

.footer-description {
  color: #a0aec0;
  line-height: 1.6;
  margin-bottom: 1.5rem;
}

.footer-links {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}

.footer-section h4 {
  color: #4facfe;
  font-size: 1.2rem;
  margin-bottom: 1.5rem;
  position: relative;
  padding-bottom: 0.5rem;
}

.footer-section h4::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 50px;
  height: 2px;
  background: linear-gradient(135deg, #00f2fe 0%, #4facfe 100%);
}

.footer-section ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer-section li {
  margin-bottom: 0.8rem;
}

.footer-section a {
  color: #a0aec0;
  text-decoration: none;
  transition: all 0.3s ease;
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
}

.footer-section a:hover {
  color: #4facfe;
  transform: translateX(5px);
}

.footer-section a i {
  font-size: 1.1rem;
}

.footer-bottom {
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  padding-top: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 1rem;
}

.footer-copyright {
  color: #a0aec0;
  font-size: 0.9rem;
}

.footer-credits {
  color: #a0aec0;
  font-size: 0.9rem;
}

.footer-credits i {
  color: #ff6b6b;
  margin: 0 0.3rem;
}

@media (max-width: 1024px) {
  .footer {
    padding: 3.5rem 0 1.8rem;
  }

  .footer-content {
    padding: 0 1.5rem;
  }

  .footer-top {
    grid-template-columns: 1fr;
    gap: 2rem;
  }

  .footer-info {
    max-width: 100%;
    text-align: center;
  }

  .footer-title {
    font-size: 1.6rem;
  }

  .footer-subtitle {
    font-size: 1rem;
  }

  .footer-description {
    font-size: 0.95rem;
    max-width: 500px;
    margin: 0 auto 1.5rem;
  }

  .footer-links {
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
  }

  .footer-section h4 {
    font-size: 1.1rem;
  }

  .footer-section a {
    font-size: 0.9rem;
  }
}

@media (max-width: 768px) {
  .footer {
    padding: 3rem 0 1.5rem;
  }

  .footer-content {
    padding: 0 1rem;
  }

  .footer-top {
    gap: 1.8rem;
  }

  .footer-title {
    font-size: 1.4rem;
  }

  .footer-subtitle {
    font-size: 0.95rem;
  }

  .footer-description {
    font-size: 0.9rem;
    max-width: 400px;
  }

  .footer-links {
    grid-template-columns: 1fr;
    gap: 1.8rem;
  }

  .footer-section {
    text-align: center;
  }

  .footer-section h4 {
    font-size: 1.05rem;
    margin-bottom: 1.2rem;
  }

  .footer-section h4::after {
    left: 50%;
    transform: translateX(-50%);
  }

  .footer-section li {
    margin-bottom: 0.6rem;
  }

  .footer-section a {
    font-size: 0.85rem;
    justify-content: center;
  }

  .footer-section a i {
    font-size: 1rem;
  }

  .footer-bottom {
    flex-direction: column;
    text-align: center;
    gap: 0.8rem;
    padding-top: 1.5rem;
  }

  .footer-copyright,
  .footer-credits {
    font-size: 0.85rem;
  }
}

@media (max-width: 480px) {
  .footer {
    padding: 2.5rem 0 1.2rem;
  }

  .footer-top {
    gap: 1.5rem;
  }

  .footer-title {
    font-size: 1.3rem;
  }

  .footer-subtitle {
    font-size: 0.9rem;
  }

  .footer-description {
    font-size: 0.85rem;
    max-width: 300px;
  }

  .footer-links {
    gap: 1.5rem;
  }

  .footer-section h4 {
    font-size: 1rem;
    margin-bottom: 1rem;
  }

  .footer-section li {
    margin-bottom: 0.5rem;
  }

  .footer-section a {
    font-size: 0.8rem;
  }

  .footer-section a i {
    font-size: 0.9rem;
  }

  .footer-bottom {
    padding-top: 1.2rem;
    gap: 0.6rem;
  }

  .footer-copyright,
  .footer-credits {
    font-size: 0.8rem;
  }
} 