.projects-section {
  background: linear-gradient(135deg, #1a1a1a 0%, #2d2d2d 100%);
  padding: 6rem 0;
  position: relative;
  overflow: hidden;
}

.projects-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url("data:image/svg+xml,%3Csvg width='60' height='60' viewBox='0 0 60 60' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%234facfe' fill-opacity='0.05'%3E%3Cpath d='M36 34v-4h-2v4h-4v2h4v4h2v-4h4v-2h-4zm0-30V0h-2v4h-4v2h4v4h2V6h4V4h-4zM6 34v-4H4v4H0v2h4v4h2v-4h4v-2H6zM6 4V0H4v4H0v2h4v4h2V6h4V4H6z'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
  opacity: 0.1;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  position: relative;
  z-index: 1;
}

.projects-header {
  text-align: center;
  margin-bottom: 4rem;
}

.section-title {
  font-size: 2.5rem;
  color: white;
  margin-bottom: 1rem;
  background: linear-gradient(135deg, #00f2fe 0%, #4facfe 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  display: inline-block;
}

.section-subtitle {
  color: #a0aec0;
  font-size: 1.1rem;
  max-width: 600px;
  margin: 0 auto;
}

.project-filters {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 3rem;
  flex-wrap: wrap;
}

.filter-btn {
  background: transparent;
  border: 2px solid #4facfe;
  color: #4facfe;
  padding: 0.5rem 1.5rem;
  border-radius: 25px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.filter-btn:hover {
  background: rgba(79, 172, 254, 0.1);
}

.filter-btn.active {
  background: linear-gradient(135deg, #00f2fe 0%, #4facfe 100%);
  color: white;
  border: none;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

.project-card {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  padding: 2rem;
  transition: all 0.3s ease;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.project-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  border-color: rgba(79, 172, 254, 0.3);
}

.project-header {
  margin-bottom: 1.5rem;
}

.project-title {
  color: white;
  font-size: 1.5rem;
  margin: 0 0 0.5rem 0;
  background: linear-gradient(135deg, #00f2fe 0%, #4facfe 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.project-date {
  color: #4facfe;
  font-size: 0.9rem;
}

.project-content {
  flex: 1;
}

.project-description {
  color: #e2e8f0;
  line-height: 1.6;
  margin-bottom: 1.5rem;
}

.project-features {
  margin-bottom: 1.5rem;
}

.project-features h4,
.project-tech h4 {
  color: white;
  margin-bottom: 1rem;
  font-size: 1.1rem;
}

.project-features ul {
  list-style: none;
  padding: 0;
  margin-bottom: 1.5rem;
}

.project-features li {
  color: #a0aec0;
  margin-bottom: 0.5rem;
  position: relative;
  padding-left: 1.5rem;
}

.project-features li::before {
  content: '•';
  color: #4facfe;
  position: absolute;
  left: 0;
}

.tech-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-bottom: 1.5rem;
}

.tech-tag {
  background: rgba(79, 172, 254, 0.1);
  color: #4facfe;
  padding: 0.3rem 0.8rem;
  border-radius: 20px;
  font-size: 0.9rem;
}

.project-footer {
  margin-top: auto;
}

.github-link {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  color: #4facfe;
  text-decoration: none;
  transition: all 0.3s ease;
}

.github-link:hover {
  color: #00f2fe;
  transform: translateX(5px);
}

.github-link i {
  font-size: 1.2rem;
}

.project-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  padding: 2rem;
}

.modal-content {
  background: #1a1a1a;
  border-radius: 12px;
  max-width: 900px;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
  position: relative;
  padding: 2rem;
}

.close-modal {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: none;
  border: none;
  color: white;
  font-size: 2rem;
  cursor: pointer;
  transition: color 0.3s ease;
}

.close-modal:hover {
  color: #4facfe;
}

.modal-content img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 8px;
}

.modal-details {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.modal-details h3 {
  color: white;
  font-size: 2rem;
  margin-bottom: 1rem;
}

.modal-details p {
  color: #a0aec0;
  margin-bottom: 1.5rem;
}

.project-link {
  display: inline-block;
  background: linear-gradient(135deg, #00f2fe 0%, #4facfe 100%);
  color: white;
  padding: 0.8rem 1.5rem;
  border-radius: 8px;
  text-decoration: none;
  font-weight: 600;
  transition: transform 0.3s ease;
  align-self: flex-start;
}

.project-link:hover {
  transform: translateY(-2px);
}

@media (max-width: 1024px) {
  .projects-section {
    padding: 5rem 0;
  }

  .container {
    padding: 0 1.5rem;
  }

  .projects-header {
    margin-bottom: 3rem;
  }

  .section-title {
    font-size: 2.2rem;
  }

  .section-subtitle {
    font-size: 1rem;
    max-width: 500px;
  }

  .projects-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
  }

  .project-card {
    padding: 1.8rem;
  }

  .project-title {
    font-size: 1.3rem;
  }

  .project-date {
    font-size: 0.85rem;
  }

  .project-description {
    font-size: 0.95rem;
  }

  .project-features h4,
  .project-tech h4 {
    font-size: 1rem;
  }

  .project-features li {
    font-size: 0.9rem;
  }

  .tech-tag {
    font-size: 0.85rem;
    padding: 0.25rem 0.7rem;
  }
}

@media (max-width: 768px) {
  .projects-section {
    padding: 4rem 0;
  }

  .container {
    padding: 0 1rem;
  }

  .projects-header {
    margin-bottom: 2.5rem;
  }

  .section-title {
    font-size: 2rem;
  }

  .section-subtitle {
    font-size: 0.9rem;
    max-width: 400px;
  }

  .projects-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
    max-width: 600px;
    margin: 0 auto;
  }

  .project-card {
    padding: 1.5rem;
  }

  .project-header {
    margin-bottom: 1.2rem;
  }

  .project-title {
    font-size: 1.2rem;
  }

  .project-date {
    font-size: 0.8rem;
  }

  .project-description {
    font-size: 0.9rem;
    margin-bottom: 1.2rem;
  }

  .project-features {
    margin-bottom: 1.2rem;
  }

  .project-features h4,
  .project-tech h4 {
    font-size: 0.95rem;
    margin-bottom: 0.8rem;
  }

  .project-features li {
    font-size: 0.85rem;
    margin-bottom: 0.4rem;
  }

  .tech-tags {
    gap: 0.4rem;
    margin-bottom: 1.2rem;
  }

  .tech-tag {
    font-size: 0.8rem;
    padding: 0.2rem 0.6rem;
  }

  .github-link {
    font-size: 0.9rem;
  }

  .github-link i {
    font-size: 1.1rem;
  }
}

@media (max-width: 480px) {
  .projects-section {
    padding: 3rem 0;
  }

  .projects-header {
    margin-bottom: 2rem;
  }

  .section-title {
    font-size: 1.8rem;
  }

  .section-subtitle {
    font-size: 0.85rem;
    max-width: 300px;
  }

  .project-card {
    padding: 1.2rem;
  }

  .project-title {
    font-size: 1.1rem;
  }

  .project-date {
    font-size: 0.75rem;
  }

  .project-description {
    font-size: 0.85rem;
    margin-bottom: 1rem;
  }

  .project-features h4,
  .project-tech h4 {
    font-size: 0.9rem;
  }

  .project-features li {
    font-size: 0.8rem;
    padding-left: 1.2rem;
  }

  .tech-tag {
    font-size: 0.75rem;
    padding: 0.15rem 0.5rem;
  }

  .github-link {
    font-size: 0.85rem;
  }

  .github-link i {
    font-size: 1rem;
  }
} 