.about-section {
  background: linear-gradient(135deg, #1a1a1a 0%, #2d2d2d 100%);
  padding: 6rem 0;
  position: relative;
  overflow: hidden;
}

.about-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url("data:image/svg+xml,%3Csvg width='60' height='60' viewBox='0 0 60 60' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%234facfe' fill-opacity='0.05'%3E%3Cpath d='M36 34v-4h-2v4h-4v2h4v4h2v-4h4v-2h-4zm0-30V0h-2v4h-4v2h4v4h2V6h4V4h-4zM6 34v-4H4v4H0v2h4v4h2v-4h4v-2H6zM6 4V0H4v4H0v2h4v4h2V6h4V4H6z'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
  opacity: 0.1;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  position: relative;
  z-index: 1;
}

.about-header {
  text-align: center;
  margin-bottom: 4rem;
  animation: fadeInDown 1s ease;
}

.section-title {
  font-size: 2.5rem;
  color: white;
  margin-bottom: 1rem;
  background: linear-gradient(135deg, #00f2fe 0%, #4facfe 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  display: inline-block;
  position: relative;
  font-weight: 700;
  letter-spacing: 1px;
}

.section-title::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 50px;
  height: 3px;
  background: linear-gradient(135deg, #00f2fe 0%, #4facfe 100%);
  border-radius: 3px;
}

.section-subtitle {
  color: #a0aec0;
  font-size: 1.2rem;
  max-width: 600px;
  margin: 1.5rem auto 0;
  font-weight: 300;
  letter-spacing: 0.5px;
}

.about-card {
  background: rgba(255, 255, 255, 0.03);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  padding: 2.5rem;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  max-width: 1200px;
  margin: 0 auto;
}

.about-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
  border-color: rgba(79, 172, 254, 0.3);
  background: rgba(255, 255, 255, 0.05);
}

.profile-section {
  display: flex;
  align-items: center;
  gap: 2.5rem;
  margin-bottom: 2.5rem;
  padding-bottom: 2.5rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.profile-image-container {
  position: relative;
  width: 160px;
  height: 160px;
  border-radius: 50%;
  overflow: hidden;
}

.profile-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
  border: 3px solid rgba(255, 255, 255, 0.1);
  position: relative;
  z-index: 1;
  transition: all 0.3s ease;
}

.profile-image:hover {
  transform: scale(1.02);
  border-color: rgba(79, 172, 254, 0.3);
}

.image-decoration {
  position: absolute;
  top: -20px;
  left: -20px;
  right: -20px;
  bottom: -20px;
  border: 2px solid rgba(79, 172, 254, 0.3);
  border-radius: 50%;
  animation: rotate 20s linear infinite;
}

.profile-info h3 {
  color: white;
  font-size: 2rem;
  margin-bottom: 0.5rem;
  font-weight: 600;
  letter-spacing: 0.5px;
}

.role {
  color: #4facfe;
  font-size: 1.2rem;
  margin-bottom: 1.2rem;
  font-weight: 500;
  letter-spacing: 0.5px;
}

.social-links {
  display: flex;
  gap: 1.2rem;
}

.social-link {
  color: #a0aec0;
  font-size: 1.3rem;
  transition: all 0.3s ease;
}

.social-link:hover {
  color: #4facfe;
  transform: translateY(-3px) scale(1.1);
}

.about-description {
  margin-bottom: 2.5rem;
}

.about-description p {
  color: #e2e8f0;
  font-size: 1.15rem;
  line-height: 1.8;
  font-weight: 300;
  letter-spacing: 0.3px;
}

.skills-section {
  margin-bottom: 2.5rem;
}

.skills-section h4 {
  color: white;
  font-size: 1.3rem;
  margin-bottom: 1.8rem;
  display: flex;
  align-items: center;
  gap: 0.8rem;
  font-weight: 600;
  letter-spacing: 0.5px;
}

.skills-section h4::before {
  content: '';
  display: block;
  width: 30px;
  height: 2px;
  background: linear-gradient(135deg, #00f2fe 0%, #4facfe 100%);
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.2rem;
}

.skill-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
  background: rgba(255, 255, 255, 0.03);
  border-radius: 10px;
  transition: all 0.3s ease;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.skill-item:hover {
  background: rgba(255, 255, 255, 0.05);
  transform: translateY(-3px);
  border-color: var(--skill-color);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.skill-item i {
  color: var(--skill-color);
  font-size: 1.3rem;
}

.skill-item span {
  color: #e2e8f0;
  font-size: 1rem;
  font-weight: 500;
}

.cta-section {
  text-align: center;
}

.cta-button {
  display: inline-flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem 2rem;
  background: linear-gradient(135deg, #00f2fe 0%, #4facfe 100%);
  color: white;
  border-radius: 10px;
  text-decoration: none;
  font-weight: 600;
  transition: all 0.3s ease;
  letter-spacing: 0.5px;
  font-size: 1.1rem;
}

.cta-button:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 20px rgba(79, 172, 254, 0.4);
}

.cta-button i {
  transition: transform 0.3s ease;
}

.cta-button:hover i {
  transform: translateX(5px);
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (max-width: 1024px) {
  .about-section {
    padding: 5rem 0;
  }

  .container {
    padding: 0 1.5rem;
  }

  .about-header {
    margin-bottom: 3rem;
  }

  .section-title {
    font-size: 2.2rem;
  }

  .section-subtitle {
    font-size: 1.1rem;
    max-width: 500px;
  }

  .about-card {
    padding: 2rem;
  }

  .profile-section {
    gap: 2rem;
    margin-bottom: 2rem;
    padding-bottom: 2rem;
  }

  .profile-image-container {
    width: 140px;
    height: 140px;
  }

  .skills-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
}

@media (max-width: 768px) {
  .about-section {
    padding: 4rem 0;
  }

  .container {
    padding: 0 1rem;
  }

  .about-header {
    margin-bottom: 2.5rem;
  }

  .section-title {
    font-size: 2rem;
  }

  .section-subtitle {
    font-size: 1rem;
    margin: 1rem auto 0;
  }

  .about-card {
    padding: 1.5rem;
  }

  .profile-section {
    flex-direction: column;
    text-align: center;
    gap: 1.5rem;
    margin-bottom: 1.5rem;
    padding-bottom: 1.5rem;
  }

  .profile-image-container {
    width: 160px;
    height: 160px;
  }

  .profile-info h3 {
    font-size: 1.8rem;
  }

  .role {
    font-size: 1.1rem;
  }

  .social-links {
    justify-content: center;
  }

  .about-description p {
    font-size: 1rem;
    text-align: center;
  }

  .skills-section h4 {
    font-size: 1.2rem;
    justify-content: center;
  }

  .skills-grid {
    grid-template-columns: 1fr;
    gap: 0.8rem;
  }

  .skill-item {
    padding: 0.8rem;
  }

  .cta-button {
    padding: 0.8rem 1.5rem;
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .about-section {
    padding: 3rem 0;
  }

  .section-title {
    font-size: 1.8rem;
  }

  .section-subtitle {
    font-size: 0.9rem;
  }

  .about-card {
    padding: 1.2rem;
  }

  .profile-image-container {
    width: 140px;
    height: 140px;
  }

  .profile-info h3 {
    font-size: 1.5rem;
  }

  .role {
    font-size: 1rem;
  }

  .social-link {
    font-size: 1.2rem;
  }

  .about-description p {
    font-size: 0.9rem;
  }

  .skills-section h4 {
    font-size: 1.1rem;
  }

  .skill-item {
    padding: 0.7rem;
  }

  .skill-item i {
    font-size: 1.1rem;
  }

  .skill-item span {
    font-size: 0.9rem;
  }

  .cta-button {
    padding: 0.7rem 1.2rem;
    font-size: 0.9rem;
  }
} 