.internship-section {
  background: linear-gradient(135deg, #1a1a1a 0%, #2d2d2d 100%);
  padding: 6rem 0;
  position: relative;
  overflow: hidden;
}

.internship-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url("data:image/svg+xml,%3Csvg width='60' height='60' viewBox='0 0 60 60' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%234facfe' fill-opacity='0.05'%3E%3Cpath d='M36 34v-4h-2v4h-4v2h4v4h2v-4h4v-2h-4zm0-30V0h-2v4h-4v2h4v4h2V6h4V4h-4zM6 34v-4H4v4H0v2h4v4h2v-4h4v-2H6zM6 4V0H4v4H0v2h4v4h2V6h4V4H6z'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
  opacity: 0.1;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  position: relative;
  z-index: 1;
}

.internship-header {
  text-align: center;
  margin-bottom: 4rem;
  animation: fadeInDown 1s ease;
}

.section-title {
  font-size: 2.5rem;
  color: white;
  margin-bottom: 1rem;
  background: linear-gradient(135deg, #00f2fe 0%, #4facfe 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  display: inline-block;
  position: relative;
}

.section-title::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 50px;
  height: 3px;
  background: linear-gradient(135deg, #00f2fe 0%, #4facfe 100%);
  border-radius: 3px;
}

.section-subtitle {
  color: #a0aec0;
  font-size: 1.1rem;
  max-width: 600px;
  margin: 1.5rem auto 0;
}

.internship-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

.internship-card {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  padding: 2.5rem;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.internship-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, rgba(79, 172, 254, 0.1) 0%, rgba(0, 242, 254, 0.1) 100%);
  opacity: 0;
  transition: opacity 0.4s ease;
}

.internship-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
  border-color: rgba(79, 172, 254, 0.3);
}

.internship-card:hover::before {
  opacity: 1;
}

.card-icon {
  position: absolute;
  top: -20px;
  right: -20px;
  width: 100px;
  height: 100px;
  background: var(--card-color);
  opacity: 0.1;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 0;
}

.card-icon i {
  font-size: 2.5rem;
  color: var(--card-color);
  opacity: 0.5;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1.5rem;
  padding-bottom: 1.5rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  position: relative;
  z-index: 1;
}

.title-group h3 {
  color: white;
  font-size: 1.5rem;
  margin: 0 0 0.5rem 0;
  background: linear-gradient(135deg, #00f2fe 0%, #4facfe 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 600;
}

.company {
  color: #4facfe;
  font-size: 1.1rem;
  font-weight: 500;
}

.meta-group {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 0.5rem;
}

.location, .date {
  color: #a0aec0;
  font-size: 0.9rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.location i, .date i {
  color: #4facfe;
}

.card-content {
  flex: 1;
  position: relative;
  z-index: 1;
}

.description {
  color: #e2e8f0;
  line-height: 1.6;
  margin-bottom: 1.5rem;
  font-size: 1.05rem;
}

.tech-stack {
  margin-bottom: 1.5rem;
}

.tech-stack h4 {
  color: white;
  margin-bottom: 1rem;
  font-size: 1.1rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.tech-stack h4::before {
  content: '';
  display: block;
  width: 20px;
  height: 2px;
  background: linear-gradient(135deg, #00f2fe 0%, #4facfe 100%);
}

.tech-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.tech-tag {
  background: rgba(79, 172, 254, 0.1);
  color: #4facfe;
  padding: 0.4rem 0.8rem;
  border-radius: 20px;
  font-size: 0.9rem;
  transition: all 0.3s ease;
  border: 1px solid rgba(79, 172, 254, 0.2);
}

.tech-tag:hover {
  background: rgba(79, 172, 254, 0.2);
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(79, 172, 254, 0.2);
}

.card-footer {
  margin-top: auto;
  padding-top: 1.5rem;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  position: relative;
  z-index: 1;
}

.certification {
  display: flex;
  align-items: center;
  gap: 1rem;
  background: rgba(79, 172, 254, 0.1);
  padding: 1rem;
  border-radius: 12px;
  border: 1px solid rgba(79, 172, 254, 0.2);
  transition: all 0.3s ease;
}

.certification:hover {
  background: rgba(79, 172, 254, 0.2);
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(79, 172, 254, 0.2);
}

.certification i {
  color: #4facfe;
  font-size: 1.8rem;
}

.certification-details {
  display: flex;
  flex-direction: column;
}

.certification-name {
  color: white;
  font-weight: 500;
  font-size: 1.1rem;
}

.certification-date {
  color: #a0aec0;
  font-size: 0.9rem;
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (max-width: 1024px) {
  .internship-section {
    padding: 5rem 0;
  }

  .container {
    padding: 0 1.5rem;
  }

  .internship-header {
    margin-bottom: 3rem;
  }

  .section-title {
    font-size: 2.2rem;
  }

  .section-subtitle {
    font-size: 1rem;
    max-width: 500px;
  }

  .internship-grid {
    gap: 1.5rem;
  }

  .internship-card {
    padding: 2rem;
  }

  .card-icon {
    width: 80px;
    height: 80px;
  }

  .card-icon i {
    font-size: 2rem;
  }

  .title-group h3 {
    font-size: 1.3rem;
  }

  .company {
    font-size: 1rem;
  }

  .description {
    font-size: 0.95rem;
  }
}

@media (max-width: 768px) {
  .internship-section {
    padding: 4rem 0;
  }

  .container {
    padding: 0 1rem;
  }

  .internship-header {
    margin-bottom: 2.5rem;
  }

  .section-title {
    font-size: 2rem;
  }

  .section-subtitle {
    font-size: 0.9rem;
    max-width: 400px;
  }

  .internship-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .internship-card {
    padding: 1.5rem;
  }

  .card-header {
    flex-direction: column;
    gap: 1rem;
    margin-bottom: 1.2rem;
    padding-bottom: 1.2rem;
  }

  .meta-group {
    align-items: flex-start;
    gap: 0.4rem;
  }

  .title-group h3 {
    font-size: 1.2rem;
  }

  .company {
    font-size: 0.95rem;
  }

  .location, .date {
    font-size: 0.85rem;
  }

  .description {
    font-size: 0.9rem;
    margin-bottom: 1.2rem;
  }

  .tech-stack h4 {
    font-size: 1rem;
  }

  .tech-tag {
    font-size: 0.85rem;
    padding: 0.3rem 0.7rem;
  }

  .certification {
    padding: 0.8rem;
  }

  .certification i {
    font-size: 1.2rem;
  }

  .certification-name {
    font-size: 0.9rem;
  }

  .certification-date {
    font-size: 0.8rem;
  }
}

@media (max-width: 480px) {
  .internship-section {
    padding: 3rem 0;
  }

  .internship-header {
    margin-bottom: 2rem;
  }

  .section-title {
    font-size: 1.8rem;
  }

  .section-subtitle {
    font-size: 0.85rem;
    max-width: 300px;
  }

  .internship-card {
    padding: 1.2rem;
  }

  .card-icon {
    width: 60px;
    height: 60px;
    top: -15px;
    right: -15px;
  }

  .card-icon i {
    font-size: 1.5rem;
  }

  .title-group h3 {
    font-size: 1.1rem;
  }

  .company {
    font-size: 0.9rem;
  }

  .location, .date {
    font-size: 0.8rem;
  }

  .description {
    font-size: 0.85rem;
    margin-bottom: 1rem;
  }

  .tech-stack h4 {
    font-size: 0.9rem;
  }

  .tech-tag {
    font-size: 0.8rem;
    padding: 0.25rem 0.6rem;
  }

  .certification {
    padding: 0.7rem;
    gap: 0.7rem;
  }

  .certification i {
    font-size: 1.1rem;
  }

  .certification-name {
    font-size: 0.85rem;
  }

  .certification-date {
    font-size: 0.75rem;
  }
} 