:root {
  --primary-color: #2563eb;
  --secondary-color: #3b82f6;
  --background-color: #f8fafc;
  --text-color: #1e293b;
  --accent-color: #f59e0b;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Inter', sans-serif;
}

body {
  background-color: var(--background-color);
  color: var(--text-color);
  line-height: 1.6;
}

.App {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 2rem;
}

section {
  padding: 5rem 0;
  scroll-margin-top: 80px;
}

.glass-effect {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 10px;
  padding: 2rem;
}

.btn {
  padding: 0.8rem 1.5rem;
  border-radius: 8px;
  font-weight: 600;
  transition: all 0.3s ease;
  cursor: pointer;
}

.btn-primary {
  background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
  color: white;
  border: none;
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(37, 99, 235, 0.2);
}

.section-title {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 2rem;
  background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

@media (max-width: 768px) {
  .App {
    padding: 0 1rem;
  }
  
  section {
    padding: 3rem 0;
  }
  
  .section-title {
    font-size: 2rem;
  }
}

@font-face {
    font-family: 'Formula font';
    src: url(./font/Formula1-Regular.otf);
}

body {
    font-family: 'Formula font',sans-serif;
    background-color: #202221;
}

header {
    background-image: url('./images/image.jpg');
    background-size: cover;
    background-position: center;
    height: 95vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
  }

  a:hover {
    transform: scale(1.2);
  }
  .custom-heading {
    font-size: 48px;
    font-weight: bold;
    margin-top: 1.5rem; /* equivalent to mt-4 */
    transition: color 0.3s, transform 0.3s;
  }
  
  .custom-heading:hover {
    color: #d1d5db; /* Tailwind's gray-300 equivalent */
    transform: scale(1.1); /* Example of scaling on hover */
  }
  
  
  footer {
    background-color: #181818;
    color: #ffffff;
  }
  