.hero-section {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: linear-gradient(135deg, #1a1a1a 0%, #2d2d2d 100%);
  position: relative;
  overflow: hidden;
  padding: 2rem 0;
}

.hero-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url("data:image/svg+xml,%3Csvg width='60' height='60' viewBox='0 0 60 60' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%234facfe' fill-opacity='0.05'%3E%3Cpath d='M36 34v-4h-2v4h-4v2h4v4h2v-4h4v-2h-4zm0-30V0h-2v4h-4v2h4v4h2V6h4V4h-4zM6 34v-4H4v4H0v2h4v4h2v-4h4v-2H6zM6 4V0H4v4H0v2h4v4h2V6h4V4H6z'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
  opacity: 0.1;
}

.hero-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1200px;
  width: 100%;
  padding: 0 2rem;
  z-index: 1;
  gap: 4rem;
}

.hero-text {
  flex: 1;
  max-width: 600px;
  animation: fadeInLeft 1s ease;
}

.hero-title {
  font-size: 4rem;
  font-weight: 800;
  margin-bottom: 1.5rem;
  line-height: 1.2;
}

.gradient-text {
  background: linear-gradient(135deg, #00f2fe 0%, #4facfe 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  display: block;
  font-size: 1.2em;
}

.name {
  color: white;
  display: block;
  font-size: 1.2em;
}

.hero-subtitle {
  font-size: 1.5rem;
  color: #4facfe;
  margin-bottom: 1.5rem;
  position: relative;
  display: inline-block;
}

.typing-text {
  border-right: 2px solid #4facfe;
  animation: blink 0.75s step-end infinite;
}

.hero-description {
  font-size: 1.1rem;
  color: #a0aec0;
  margin-bottom: 2rem;
  max-width: 500px;
  line-height: 1.8;
}

.hero-social {
  display: flex;
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.social-link {
  color: #a0aec0;
  font-size: 1.5rem;
  transition: all 0.3s ease;
  position: relative;
}

.social-link::after {
  content: '';
  position: absolute;
  bottom: -5px;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 2px;
  background: linear-gradient(135deg, #00f2fe 0%, #4facfe 100%);
  transition: width 0.3s ease;
}

.social-link:hover {
  color: #4facfe;
  transform: translateY(-3px);
}

.social-link:hover::after {
  width: 100%;
}

.hero-buttons {
  display: flex;
  gap: 1rem;
}

.btn {
  padding: 0.8rem 2rem;
  border-radius: 8px;
  font-weight: 600;
  text-decoration: none;
  transition: all 0.3s ease;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.btn-primary {
  background: linear-gradient(135deg, #00f2fe 0%, #4facfe 100%);
  color: white;
  border: none;
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(79, 172, 254, 0.4);
}

.btn-outline {
  background: transparent;
  border: 2px solid #4facfe;
  color: #4facfe;
}

.btn-outline:hover {
  background: #4facfe;
  color: white;
  transform: translateY(-2px);
}

.hero-image {
  position: relative;
  width: 400px;
  height: 400px;
  animation: fadeInRight 1s ease;
}

.profile-image {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border: 2px solid rgba(255, 255, 255, 0.2);
  overflow: hidden;
}

.floating-shapes {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.shape {
  position: absolute;
  background: linear-gradient(135deg, #00f2fe 0%, #4facfe 100%);
  border-radius: 50%;
  opacity: 0.3;
}

.shape-1 {
  width: 100px;
  height: 100px;
  top: -20px;
  left: -20px;
  animation: float 6s ease-in-out infinite;
}

.shape-2 {
  width: 150px;
  height: 150px;
  bottom: -30px;
  right: -30px;
  animation: float 8s ease-in-out infinite;
}

.shape-3 {
  width: 80px;
  height: 80px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  animation: float 7s ease-in-out infinite;
}

.scroll-indicator {
  position: absolute;
  bottom: 2rem;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  color: #a0aec0;
  font-size: 0.9rem;
  animation: bounce 2s infinite;
}

.scroll-arrow {
  width: 20px;
  height: 20px;
  border-right: 2px solid #4facfe;
  border-bottom: 2px solid #4facfe;
  transform: rotate(45deg);
  animation: arrowBounce 2s infinite;
}

@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translateX(-50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translateX(50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes float {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-20px);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-10px);
  }
  60% {
    transform: translateY(-5px);
  }
}

@keyframes arrowBounce {
  0%, 20%, 50%, 80%, 100% {
    transform: rotate(45deg) translateY(0);
  }
  40% {
    transform: rotate(45deg) translateY(-5px);
  }
  60% {
    transform: rotate(45deg) translateY(-2px);
  }
}

@keyframes blink {
  from, to {
    border-color: transparent;
  }
  50% {
    border-color: #4facfe;
  }
}

@media (max-width: 1024px) {
  .hero-content {
    gap: 3rem;
    padding: 0 1.5rem;
  }

  .hero-text {
    max-width: 500px;
  }

  .hero-title {
    font-size: 3.5rem;
  }

  .hero-description {
    font-size: 1rem;
    max-width: 450px;
  }

  .hero-social {
    gap: 1.2rem;
  }

  .hero-buttons {
    gap: 0.8rem;
  }

  .hero-image {
    width: 350px;
    height: 350px;
  }
}

@media (max-width: 768px) {
  .hero-content {
    flex-direction: column;
    text-align: center;
    gap: 2rem;
    padding: 0 1rem;
  }

  .hero-text {
    max-width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .hero-title {
    font-size: 2.8rem;
    margin-bottom: 1rem;
  }

  .hero-subtitle {
    font-size: 1.3rem;
    margin-bottom: 1rem;
  }

  .hero-description {
    font-size: 1rem;
    max-width: 100%;
    margin-bottom: 1.5rem;
  }

  .hero-social {
    justify-content: center;
    margin-bottom: 1.5rem;
  }

  .hero-buttons {
    flex-direction: column;
    width: 100%;
    max-width: 300px;
  }

  .btn {
    width: 100%;
    padding: 0.8rem 1.5rem;
  }

  .hero-image {
    width: 280px;
    height: 280px;
    margin: 0 auto;
  }

  .shape-1 {
    width: 80px;
    height: 80px;
  }

  .shape-2 {
    width: 120px;
    height: 120px;
  }

  .shape-3 {
    width: 60px;
    height: 60px;
  }
}

@media (max-width: 480px) {
  .hero-section {
    padding: 1.5rem 0;
  }

  .hero-title {
    font-size: 2.2rem;
  }

  .hero-subtitle {
    font-size: 1.1rem;
  }

  .hero-description {
    font-size: 0.9rem;
  }

  .hero-image {
    width: 240px;
    height: 240px;
  }

  .social-link {
    font-size: 1.3rem;
  }

  .scroll-indicator {
    bottom: 1rem;
    font-size: 0.8rem;
  }
} 